	module.exports = {"data":{"postsJson":{"title":"Власть, Чудеса и Вера","questionsTitle":"СРАВНЕНИЕ, КОНТРАСТ И ПОВТОРЕНИЕ","display":"block","bookName":"От Луки","ref":"7:1-8:56","html":"<p>   <strong>7</strong><u>1</u>Когда Он окончил все слова Свои к слушавшему народу, то вошел в Капернаум.<br />   <u>2</u>У одного сотника слуга, которым он дорожил, был болен при смерти.<br />   <u>3</u>Услышав об Иисусе, он послал к Нему Иудейских старейшин просить Его, чтобы пришел исцелить слугу его.<br />   <u>4</u>И они, придя к Иисусу, просили Его убедительно, говоря: </p><h1>Некоторые лидеры</h1><h4>он достоин, чтобы Ты сделал для него это,<br />   <u>5</u>ибо он любит народ наш и построил нам синагогу.</h4><p> <u>6</u>Иисус пошел с ними. И когда Он недалеко уже был от дома, сотник прислал к Нему друзей сказать Ему: </p><h1>Римский офицер</h1><h4>не трудись, Господи! ибо я недостоин, чтобы Ты вошел под кров мой;<br />   <u>7</u>потому и себя самого не почел я достойным придти к Тебе; но скажи слово, и выздоровеет слуга мой.<br />   <u>8</u>Ибо я и подвластный человек, но, имея у себя в подчинении воинов,s говорю одному: пойди, и идет; и другому: приди, и приходит; и слуге моему: сделай то, и делает.</h4><p> <u>9</u>Услышав сие, Иисус удивился ему и, обратившись, сказал идущему за Ним народу: </p><h1>Иисус</h1><h2>сказываю вам, что и в Израиле не нашел Я такой веры.</h2><p> <u>10</u>Посланные, возвратившись в дом, нашли больного слугу выздоровевшим.<br />   <u>11</u>После сего Иисус пошел в город, называемый Наин; и с Ним шли многие из учеников Его и множество народа.<br />   <u>12</u>Когда же Он приблизился к городским воротам, тут выносили умершего, единственного сына у матери, а она была вдова; и много народа шло с нею из города.<br />   <u>13</u>Увидев ее, Господь сжалился над нею и сказал ей: </p><h1>Иисус</h1><h2>не плачь.</h2><p> <u>14</u>И, подойдя, прикоснулся к одру; несшие остановились, и Он сказал: </p><h1>Иисус</h1><h2>юноша! тебе говорю, встань!</h2><p> <u>15</u>Мертвый, поднявшись, сел и стал говорить; и отдал его [Иисус] матери его.<br />   <u>16</u>И всех объял страх, и славили Бога, говоря: </p><h1>Люди в Наин</h1><h4>великий пророк восстал между нами, и Бог посетил народ Свой.</h4><p> <u>17</u>Такое мнение о Нём распространилось по всей Иудее и по всей окрестности.<br />   <u>18</u>И возвестили Иоанну ученики его о всём том.<br />   <u>19</u>Иоанн, призвав двоих из учеников своих, послал к Иисусу спросить: </p><h1>Иран Криститель</h1><h4>Ты ли Тот, Который должен придти, или ожидать нам другого?</h4><p> <u>20</u>Они, придя к [Иисусу], сказали:</p><h1>Ученики Иоанна</h1><h4>Иоанн Креститель послал нас к Тебе спросить: Ты ли Тот, Которому должно придти, или другого ожидать нам?</h4><p> <u>21</u>А в это время Он многих исцелил от болезней и недугов и от злых духов, и многим слепым даровал зрение.<br />   <u>22</u>И сказал им Иисус в ответ: </p><h1>Иисус</h1><h2>пойдите, скажите Иоанну, что вы видели и слышали: слепые прозревают, хромые ходят, прокаженные очищаются, глухие слышат, мертвые воскресают, нищие благовествуют;<br />   <u>23</u>и блажен, кто не соблазнится о Мне!</h2><p> <u>24</u>По отшествии же посланных Иоанном, начал говорить к народу об Иоанне: </p><h1>Иисус</h1><h2>что смотреть ходили вы в пустыню? трость ли, ветром колеблемую?<br />   <u>25</u>Что же смотреть ходили вы? человека ли, одетого в мягкие одежды? Но одевающиеся пышно и роскошно живущие находятся при дворах царских.<br />   <u>26</u>Что же смотреть ходили вы? пророка ли? Да, говорю вам, и больше пророка.<br />   <u>27</u>Сей есть, о котором написано: вот, Я посылаю Ангела Моего пред лицем Твоим, который приготовит путь Твой пред Тобою.<br />   <u>28</u>Ибо говорю вам: из рожденных женами нет ни одного пророка больше Иоанна Крестителя; но меньший в Царствии Божием больше его.</h2><p> <u>29</u>И весь народ, слушавший [Его], и мытари воздали славу Богу, крестившись крещением Иоанновым;<br />   <u>30</u>а фарисеи и законники отвергли волю Божию о себе, не крестившись от него.<br />   <u>31</u>Тогда Господь сказал: </p><h1>Иисус</h1><h2>с кем сравню людей рода сего? и кому они подобны?<br />   <u>32</u>Они подобны детям, которые сидят на улице, кличут друг друга и говорят: мы играли вам на свирели, и вы не плясали; мы пели вам плачевные песни, и вы не плакали.<br />   <u>33</u>Ибо пришел Иоанн Креститель: ни хлеба не ест, ни вина не пьет; и говорите: в нем бес.<br />   <u>34</u>Пришел Сын Человеческий: ест и пьет; и говорите: вот человек, который любит есть и пить вино, друг мытарям и грешникам.<br />   <u>35</u>И оправдана премудрость всеми чадами ее.</h2><p> <u>36</u>Некто из фарисеев просил Его вкусить с ним пищи; и Он, войдя в дом фарисея, возлег.<br />   <u>37</u>И вот, женщина того города, которая была грешница, узнав, что Он возлежит в доме фарисея, принесла алавастровый сосуд с миром<br />   <u>38</u>и, став позади у ног Его и плача, начала обливать ноги Его слезами и отирать волосами головы своей, и целовала ноги Его, и мазала миром.<br />   <u>39</u>Видя это, фарисей, пригласивший Его, сказал сам в себе: </p><h1>Симеон Фарисей</h1><h4>если бы Он был пророк, то знал бы, кто и какая женщина прикасается к Нему, ибо она грешница.</h4><p> <u>40</u>Обратившись к нему, Иисус сказал: </p><h1>Иисус</h1><h2>Симон! Я имею нечто сказать тебе. </h2><p>Он говорит: </p><h1>Симеон Фарисей</h1><h4>скажи, Учитель.</h4><p> <u>41</u>Иисус сказал: </p><h1>Иисус</h1><h2>у одного заимодавца было два должника: один должен был пятьсот динариев, а другой пятьдесят,<br />   <u>42</u>но как они не имели чем заплатить, он простил обоим. Скажи же, который из них более возлюбит его?</h2><p> <u>43</u>Симон отвечал: </p><h1>Симеон Фарисей</h1><h4>думаю, тот, которому более простил. </h4><p>Он сказал ему: </p><h1>Иисус</h1><h2>правильно ты рассудил.</h2><p> <u>44</u>И, обратившись к женщине, сказал Симону: </p><h1>Иисус</h1><h2>видишь ли ты эту женщину? Я пришел в дом твой, и ты воды Мне на ноги не дал, а она слезами облила Мне ноги и волосами головы своей отёрла;<br />   <u>45</u>ты целования Мне не дал, а она, с тех пор как Я пришел, не перестает целовать у Меня ноги;<br />   <u>46</u>ты головы Мне маслом не помазал, а она миром помазала Мне ноги.<br />   <u>47</u>А потому сказываю тебе: прощаются грехи её многие за то, что она возлюбила много, а кому мало прощается, тот мало любит.</h2><p> <u>48</u>Ей же сказал: </p><h1>Иисус</h1><h2>прощаются тебе грехи.</h2><p> <u>49</u>И возлежавшие с Ним начали говорить про себя: </p><h1>Некоторые в доме Симена</h1><h4>кто это, что и грехи прощает?</h4><p> <u>50</u>Он же сказал женщине: </p><h2>вера твоя спасла тебя, иди с миром.</h2><p>   <strong>8</strong><u>1</u>После сего Он проходил по городам и селениям, проповедуя и благовествуя Царствие Божие, и с Ним двенадцать,<br />   <u>2</u>и некоторые женщины, которых Он исцелил от злых духов и болезней: Мария, называемая Магдалиною, из которой вышли семь бесов,<br />   <u>3</u>и Иоанна, жена Хузы, домоправителя Иродова, и Сусанна, и многие другие, которые служили Ему имением своим.<br />   <u>4</u>Когда же собралось множество народа, и из всех городов жители сходились к Нему, Он начал говорить притчею:</p><h1>Иисус</h1><h2> <u>5</u>вышел сеятель сеять семя свое, и когда он сеял, иное упало при дороге и было потоптано, и птицы небесные поклевали его;<br />   <u>6</u>а иное упало на камень и, взойдя, засохло, потому что не имело влаги;<br />   <u>7</u>а иное упало между тернием, и выросло терние и заглушило его;<br />   <u>8</u>а иное упало на добрую землю и, взойдя, принесло плод сторичный. </h2><p>Сказав сие, возгласил: </p><h1>Иисус</h1><h2>кто имеет уши слышать, да слышит!</h2><p> <u>9</u>Ученики же Его спросили у Него: </p><h1>Ученики</h1><h3>что бы значила притча сия?</h3><p> <u>10</u>Он сказал: </p><h1>Иисус</h1><h2>вам дано знать тайны Царствия Божия, а прочим в притчах, так что они видя не видят и слыша не разумеют.<br />   <u>11</u>Вот что значит притча сия: семя есть слово Божие;<br />   <u>12</u>а упавшее при пути, это суть слушающие, к которым потом приходит диавол и уносит слово из сердца их, чтобы они не уверовали и не спаслись;<br />   <u>13</u>а упавшее на камень, это те, которые, когда услышат слово, с радостью принимают, но которые не имеют корня, и временем веруют, а во время искушения отпадают;<br />   <u>14</u>а упавшее в терние, это те, которые слушают слово, но, отходя, заботами, богатством и наслаждениями житейскими подавляются и не приносят плода;<br />   <u>15</u>а упавшее на добрую землю, это те, которые, услышав слово, хранят его в добром и чистом сердце и приносят плод в терпении. Сказав это, Он возгласил: кто имеет уши слышать, да слышит!<br />   <u>16</u>Никто, зажегши свечу, не покрывает ее сосудом, или не ставит под кровать, а ставит на подсвечник, чтобы входящие видели свет.<br />   <u>17</u>Ибо нет ничего тайного, что не сделалось бы явным, ни сокровенного, что не сделалось бы известным и не обнаружилось бы.<br />   <u>18</u>Итак, наблюдайте, как вы слушаете: ибо, кто имеет, тому дано будет, а кто не имеет, у того отнимется и то, что он думает иметь.</h2><p> <u>19</u>И пришли к Нему Матерь и братья Его, и не могли подойти к Нему по причине народа.<br />   <u>20</u>И дали знать Ему: </p><h1>Кто-то из Капернаума</h1><h4>Матерь и братья Твои стоят вне, желая видеть Тебя.</h4><p> <u>21</u>Он сказал им в ответ: </p><h1>Иисус</h1><h2>матерь Моя и братья Мои суть слушающие слово Божие и исполняющие его.</h2><p> <u>22</u>В один день Он вошел с учениками Своими в лодку и сказал им: </p><h1>Иисус</h1><h2>переправимся на ту сторону озера. </h2><p>И отправились.<br />   <u>23</u>Во время плавания их Он заснул. На озере поднялся бурный ветер, и заливало их [волнами], и они были в опасности.<br />   <u>24</u>И, подойдя, разбудили Его и сказали: </p><h1>Ученики</h1><h3>Наставник! Наставник! погибаем. </h3><p>Но Он, встав, запретил ветру и волнению воды; и перестали, и сделалась тишина.<br />   <u>25</u>Тогда Он сказал им: </p><h1>Иисус</h1><h2>где вера ваша? </h2><p>Они же в страхе и удивлении говорили друг другу: </p><h1>Ученики</h1><h3>кто же это, что и ветрам повелевает и воде, и повинуются Ему?</h3><p> <u>26</u>И приплыли в страну Гадаринскую, лежащую против Галилеи.<br />   <u>27</u>Когда же вышел Он на берег, встретил Его один человек из города, одержимый бесами с давнего времени, и в одежду не одевавшийся, и живший не в доме, а в гробах.<br />   <u>28</u>Он, увидев Иисуса, вскричал, пал пред Ним и громким голосом сказал: </p><h1>Человек одержимый легионам</h1><h4>что Тебе до меня, Иисус, Сын Бога Всевышнего? умоляю Тебя, не мучь меня.</h4><p> <u>29</u>Ибо [Иисус] повелел нечистому духу выйти из сего человека, потому что он долгое время мучил его, так что его связывали цепями и узами, сберегая его; но он разрывал узы и был гоним бесом в пустыни.<br />   <u>30</u>Иисус спросил его: </p><h1>Иисус</h1><h2>как тебе имя? </h2><p>Он сказал: </p><h1>Человек одержимый легионам</h1><h4>легион, </h4><p>--потому что много бесов вошло в него.<br />   <u>31</u>И они просили Иисуса, чтобы не повелел им идти в бездну.<br />   <u>32</u>Тут же на горе паслось большое стадо свиней; и [бесы] просили Его, чтобы позволил им войти в них. Он позволил им.<br />   <u>33</u>Бесы, выйдя из человека, вошли в свиней, и бросилось стадо с крутизны в озеро и потонуло.<br />   <u>34</u>Пастухи, видя происшедшее, побежали и рассказали в городе и в селениях.<br />   <u>35</u>И вышли видеть происшедшее; и, придя к Иисусу, нашли человека, из которого вышли бесы, сидящего у ног Иисуса, одетого и в здравом уме; и ужаснулись.<br />   <u>36</u>Видевшие же рассказали им, как исцелился бесновавшийся.<br />   <u>37</u>И просил Его весь народ Гадаринской окрестности удалиться от них, потому что они объяты были великим страхом. Он вошел в лодку и возвратился.<br />   <u>38</u>Человек же, из которого вышли бесы, просил Его, чтобы быть с Ним. Но Иисус отпустил его, сказав:</p><h1>Иисус</h1><h2> <u>39</u>возвратись в дом твой и расскажи, что сотворил тебе Бог. </h2><p>Он пошел и проповедывал по всему городу, что сотворил ему Иисус.<br />   <u>40</u>Когда же возвратился Иисус, народ принял Его, потому что все ожидали Его.<br />   <u>41</u>И вот, пришел человек, именем Иаир, который был начальником синагоги; и, пав к ногам Иисуса, просил Его войти к нему в дом,<br />   <u>42</u>потому что у него была одна дочь, лет двенадцати, и та была при смерти. Когда же Он шел, народ теснил Его.<br />   <u>43</u>И женщина, страдавшая кровотечением двенадцать лет, которая, издержав на врачей всё имение, ни одним не могла быть вылечена,<br />   <u>44</u>подойдя сзади, коснулась края одежды Его; и тотчас течение крови у ней остановилось.<br />   <u>45</u>И сказал Иисус: </p><h1>Иисус</h1><h2>кто прикоснулся ко Мне? </h2><p>Когда же все отрицались, Петр сказал и бывшие с Ним: </p><h1>Симен Петр</h1><h3>Наставник! народ окружает Тебя и теснит, --и Ты говоришь: кто прикоснулся ко Мне?</h3><p> <u>46</u>Но Иисус сказал: </p><h1>Иисус</h1><h2>прикоснулся ко Мне некто, ибо Я чувствовал силу, исшедшую из Меня.</h2><p> <u>47</u>Женщина, видя, что она не утаилась, с трепетом подошла и, пав пред Ним, объявила Ему перед всем народом, по какой причине прикоснулась к Нему и как тотчас исцелилась.<br />   <u>48</u>Он сказал ей: </p><h1>Иисус</h1><h2>дерзай, дщерь! вера твоя спасла тебя; иди с миром.</h2><p> <u>49</u>Когда Он еще говорил это, приходит некто из дома начальника синагоги и говорит ему: </p><h1>Посланники Иаира</h1><h4>дочь твоя умерла; не утруждай Учителя.</h4><p> <u>50</u>Но Иисус, услышав это, сказал ему: </p><h1>Иисус</h1><h2>не бойся, только веруй, и спасена будет.</h2><p> <u>51</u>Придя же в дом, не позволил войти никому, кроме Петра, Иоанна и Иакова, и отца девицы, и матери.<br />   <u>52</u>Все плакали и рыдали о ней. Но Он сказал: </p><h1>Иисус</h1><h2>не плачьте; она не умерла, но спит.</h2><p> <u>53</u>И смеялись над Ним, зная, что она умерла.<br />   <u>54</u>Он же, выслав всех вон и взяв ее за руку, возгласил: </p><h1>Иисус</h1><h2>девица! Встань.</h2><p> <u>55</u>И возвратился дух ее; она тотчас встала, и Он велел дать ей есть.<br />   <u>56</u>И удивились родители ее. Он же повелел им не сказывать никому о происшедшем.</p>","path":"/329","Question1":"Для сравнения следует связать двух или более похожих лиц, идей или ситуаций. Можете ли вы найти какие-либо сравнения, сделанные автором этой истории? Почему вы думаете, что автор сделал бы это сравнение?","Question2":"Контрастность заключается в том, чтобы ставить бок о бок два или более отдельных человека, идеи или ситуации. Можете ли вы найти какие-либо контрасты, сделанные автором этой истории? Почему вы думаете, что автор сделал бы этот контраст?","Question3":"Найдите время, чтобы отметить любые повторяющиеся слова или понятия, которые вы нашли в этой истории","Question4":"Пожалуйста, найдите время, чтобы написать короткую молитву Богу, ответив на то, что вы обнаружили через эти сравнения, контрасты и повторения.","prevPath":"/328","nextPath":"/330"}},"pathContext":{}}

